import { mapMutations } from 'vuex';
export default {
	data() {
		return {
			login: false,
			avatarUrl: '../../static/img/logo.png',
			userinfo: {}
		};
	},
	onShow() {
        console.log( this.$store )
		var hasLogin = this.$store.state.hasLogin;
		this.login = hasLogin;
		if(hasLogin){
			this.userinfo = this.$store.state.userinfo
		}
	},
	methods: {
		...mapMutations(['logout']),
		goLogin() {
			if (!this.login) {
				this.navigateTo('1', true);
			}
		},
		navigateTo(url, hasLogin) {
			if (hasLogin && !this.login) {
				uni.navigateTo({
					url: './login'
				});
			} else {
				uni.navigateTo({
					url: url
				});
			}
		},
		loginout(){
            uni.showModal({
                title:"提示信息",
                content:"您确定退出当前帐号吗？",
                success: (r) => {
                    if(r.confirm){
                        this.logout();
                        var hasLogin = this.$store.state.hasLogin;
                        this.login = hasLogin;
                    }
                }
            })
			
		}
	}
};
